@import "../Variables";
@import "../Media-mixin";

.price-selector {
    display: flex;
    align-items: center;
    gap: 10px;
}

#price {
    &-low,
    &-high {
        width: 100px;
        height: 25px;
        border-radius: 8px;
        background-color: rgba(0, 189, 157, 0.15);
        border: none;
        text-align: center;
    }
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
    -webkit-appearance: none;
    margin: 0;
}

input:focus {
    outline: none;
}
