@import "../Media-mixin";
@import "../Variables";

.pagination {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 240px;
    height: 35px;

    &__button {
        width: 20px;
        height: 20px;
        border: none;
        background-color: transparent;
        cursor: pointer;
    }
}

.button {
    &__right {
        transform: rotate(180deg);
    }
}

.list {
    display: flex;
    align-items: center;
    gap: 10px;

    &__number {
        display: flex;
        align-items: center;
        justify-content: center;
        width: 20px;
        height: 20px;
        font-size: $--font-primary;
        font-weight: $--weight-semibold;
        color: $--2--black-secondary;
        text-decoration: none;
        margin: 0;
        background: transparent;
        border: none;
        cursor: pointer;
    }

    &__number.active {
        display: flex;
        align-items: center;
        justify-content: center;
        outline: 7px solid rgba(0, 189, 157, 0.15);
        border-radius: 1px;
        background-color: rgba(0, 189, 157, 0.15);
    }
}
